.testimonials {
  &__item {
    margin-bottom: 48px;

    &:last-child {
      margin-bottom: 0;
    }

    @include onDesktop {
      margin-bottom: 0;
    }
  }
}

.testimonial {
  &__photo-continer {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;

    margin-bottom: 28px;

    @include onDesktop {
      margin-bottom: 32px;
    }
  }

  &__photo {
    width: 100%;
    grid-column: 2 / 4;
    border-radius: 50%;
    background-position: center;
    transition: 0.4s ease-in-out;

    object-fit: cover;
    object-position: center;

    &:hover {
      transform: scale(1.02);
      transition: 0.3s;
    }
  }

  &__name {
    margin: 0 0 4px;
    font-weight: bold;
    font-style: 24px;
    line-height: 30px;
    text-align: center;
    color: $colorBlue;
  }

  &__role {
    margin: 0 0 4px;
    font-style: 14px;
    line-height: 27px;
    text-align: center;
    color: #9fadc2;
  }

  &__about {
    text-align: center;
  }
}

.link {
  color: $colorBlue;
  text-decoration: none;
}
